import { Component, OnInit } from '@angular/core';
import { OpportunitiesService } from '../services/opportunities.service'
import { CookieService } from 'ngx-cookie-service';
import { MessageService } from '../services/message.service'
import { Opportunity } from '../shared/oppy';
import picasso from 'picasso.js';
import PicassoCharts from '../shared/PicassoCharts.js';

@Component({
  selector: 'app-opportunities',
  templateUrl: './opportunities.component.html',
  styleUrls: ['./opportunities.component.css']
})
export class OpportunitiesComponent implements OnInit {

  private gameID: string=null;
  private opportunities=[];

  constructor(private opportunitiesService: OpportunitiesService, 
              private cookieService: CookieService) { }

  ngOnInit() {

    this.gameID = this.cookieService.get('gameID');

    this.opportunitiesService.getOpportunities(this.gameID)
    .subscribe( res =>{
      this.opportunities=res['data'];

      picasso.chart({
        element: document.querySelector('#chartOpportunities'), // This is the element to render the chart in
        data: [{
          type: 'matrix',
          data: [
            ['Year', 'Month', 'Sales', 'Margin'],
            ['2010', 'Jan', 1106, 7],
            ['2010', 'Feb', 5444, 53],
            ['2010', 'Mar', 147, 64],
            ['2010', 'Apr', 7499, 47],
            ['2010', 'May', 430, 62],
            ['2010', 'June', 9735, 13],
            ['2010', 'July', 7435, 15],
            ['2011', 'Jan', 1482, 45],
            ['2011', 'Feb', 2659, 76],
            ['2011', 'Mar', 1261, 73],
            ['2011', 'Apr', 3085, 56],
            ['2011', 'May', 3035, 91],
            ['2011', 'June', 7691, 88],
            ['2011', 'July', 3012, 81],
            ['2012', 'Jan', 7980, 61],
            ['2012', 'Feb', 2564, 22],
            ['2012', 'Mar', 7957, 98],
            ['2012', 'Apr', 5809, 1],
            ['2012', 'May', 429, 2],
            ['2012', 'June', 6757, 77],
            ['2012', 'July', 9415, 92]
          ]
        }],
        settings: PicassoCharts.scatterplot
      })
    })
  }

}
